<script>
   /*************************************************************/
   /* AppPlots component for Test.       tab                    */
   /* --------------------------------------                    */
   /* defines plot components, controls and parameters          */
   /*************************************************************/

   import { getDefaults } from 'svelte-controls-basic';
   import { Container, Button } from 'svelte-controls-basic';
   import { getCI } from '../../../shared/utils';

   // global plot router
   import PlotsRouter from '../../../shared/plots/PlotsRouter.svelte';

   // import plot components
   import PlotAcceptance from '../../../shared/plots/PlotAcceptance.svelte';
   import PlotDistances from '../../../shared/plots/PlotDistances.svelte';
   import PlotExtremes from '../../../shared/plots/PlotExtremes.svelte';
   import PlotAliens from '../../../shared/plots/PlotAliens.svelte';
   import PlotClassStat from '../../../shared/plots/PlotClassStat.svelte';
   import PlotScores from '../../../shared/plots/PlotScores.svelte';
   import PlotSelectivity from '../../../shared/plots/PlotSelectivity.svelte';

   // import plot controls components
   import { CAT_COLORS } from '../../../shared/plots/Defaults';
   import { getPlotAcceptanceControls, getPlotDistancesControls, getPlotExtremesControls, getPlotAliensControls,
            getPlotClassStatControls, getPlotScoresControls, getPlotSelectivityControls } from '../../../shared/plots/Controls.svelte.js';

   let {
      model,
      simca,
      testset,
      testres,
      markOutlier,
      showContributions,
      resetSelection
   } = $props();



   // because most of the plot components expect JSON with results
   // assuming there is more than one result object we create it
   // same for the data
   const res = {'test': testres.pca};
   const simcares = {'test': testres.simca};
   const data = {'test': testset};

   // this is derived as simca results can change without reloading the component
   const classres = $derived({'test': testres.simca});

   // colors for acceptance plot
   const classColmapMembers = {'regular' : CAT_COLORS.regular, 'extreme': CAT_COLORS.extreme, 'outlier': CAT_COLORS.outlier};
   const classColmapStrangers = {'alien' : CAT_COLORS.alien, 'external': CAT_COLORS.external};

   // options for acceptance plot
   const acceptanceOptions = $derived.by(() => {
      const res = testres.simca.v[simca.ncomp - 1];
      return {
         'members' : {
            classColmap: classColmapMembers,
            labels: [`regular: ${res.rolsNum.regular}`, `extreme: ${res.rolsNum.extreme}`, `outliers: ${res.rolsNum.outlier}`],
            values: res.rols,
            ind: testres.simca.ind.members
         },
         'strangers': {
            classColmap: classColmapStrangers,
            labels: [`aliens: ${res.rolsNum.alien}`, `external: ${res.rolsNum.external}`],
            values: res.rols,
            ind: testres.simca.ind.strangers
         }
      }
   });

   // JSON with plot components (scope is needed for providing correct properties)
   const plots = $derived.by(() => {
      if (testres.simca.nMembers > 0 && testres.simca.nStrangers > 0) {
         return {
            acceptance: {label: 'Acceptance', scope: 'objects', component: PlotAcceptance, buttons: buttons2},
            distances: {label: 'Distances', scope: 'objects', component: PlotDistances, buttons: buttons2},
            extremes: {label: 'Extremes', scope: 'stats', component: PlotExtremes},
            aliens: {label: 'Aliens', scope: 'stats', component: PlotAliens},
            foms: {label: 'FoMs', scope: 'stats', component: PlotClassStat},
            sel: {label: 'Selectivity', scope: 'stats', component: PlotSelectivity},
            scores: {label: 'Scores', scope: 'objects', component: PlotScores, buttons: buttons1},
         }
      }

      if (testres.simca.nMembers > 0) {
         return {
            acceptance: {label: 'Acceptance', scope: 'objects', component: PlotAcceptance, buttons: buttons2},
            distances: {label: 'Distances', scope: 'objects', component: PlotDistances, buttons: buttons2},
            extremes: {label: 'Extremes', scope: 'stats', component: PlotExtremes},
            foms: {label: 'FoMs', scope: 'stats', component: PlotClassStat},
            scores: {label: 'Scores', scope: 'objects', component: PlotScores, buttons: buttons1},
         }
      }

      return {
         acceptance: {label: 'Acceptance', scope: 'objects', component: PlotAcceptance, buttons: buttons2},
         distances: {label: 'Distances', scope: 'objects', component: PlotDistances, buttons: buttons2},
         aliens: {label: 'Aliens', scope: 'stats', component: PlotAliens},
         foms: {label: 'FoMs', scope: 'stats', component: PlotClassStat},
         sel: {label: 'Selectivity', scope: 'stats', component: PlotSelectivity},
         scores: {label: 'Scores', scope: 'objects', component: PlotScores, buttons: buttons1},
      }
   });

   // JSON with plots controls (as Widget properties)
   const controls = $state({
      acceptance: getPlotAcceptanceControls(simcares),
      distances: getPlotDistancesControls(res),
      extremes: getPlotExtremesControls(['test']),
      aliens: getPlotAliensControls(['test']),
      foms: getPlotClassStatControls(simcares),
      sel: getPlotSelectivityControls(['test']),
      scores: getPlotScoresControls(res),
   })

   // JSON with currently selected plots parameters
   let params = $state({
      acceptance: getDefaults(controls.acceptance),
      distances: getDefaults(controls.distances),
      extremes: getDefaults(controls.extremes),
      aliens: getDefaults(controls.aliens),
      foms: getDefaults(controls.foms),
      sel: getDefaults(controls.sel),
      scores: getDefaults(controls.scores),
   });

   // add confidence interval values for sensitivity plot
   $effect(() => {
      const ci = {};
      if (testres.simca.nMembers > 0) {
         ci['sens'] = getCI(testres.simca.nMembers, simca.alpha);
      }
      params.foms.ci = ci;
   });

   $effect(() => {
      if (params.acceptance.subset) resetSelection();
   })

   // add additional fixed parameters for the acceptance plot
   $effect(() => {
      params.acceptance['ncomp'] = simca.ncomp;
      params.acceptance['title'] = `Acceptance (A = ${simca.ncomp})`;
      params.acceptance['limType'] = simca.limType;
      params.acceptance['alpha'] = simca.alpha;
      params.acceptance['gamma'] = simca.gamma;
   });

   // if limType or result objects changed make updates to acceptance plot fixed parameters
   $effect(() => {
      if (acceptanceOptions && params.acceptance.subset) {
         params.acceptance['colorByObj'] = params.acceptance.subset !== 'all' ? acceptanceOptions[params.acceptance.subset] : null;
      }
   });

   $effect(() => {
      params.acceptance['colorBy'] = params.acceptance.subset !== 'all' ? 'roles' : 'classes';
   });

   // reactive effects for scores plot controls
   $effect(() => {
      if (params.scores.plotType !== 'p') {
         controls.scores.comp1.hidden = true;
         controls.scores.showEllipse.hidden = true;
         params.scores.showEllipse = false;
      } else {
         controls.scores.comp1.hidden = false;
         controls.scores.showEllipse.hidden = false;
      }

      if (params.scores.plotType === 'l' || params.scores.plotType === 'b') {
         controls.scores.colorBy.hidden = true;
         params.scores.colorBy = 'none';
      } else {
         controls.scores.colorBy.hidden = false;
      }

      if (params.scores.colorBy === 'variable') {
         controls.scores.colorByVar.hidden = false;
      } else {
         controls.scores.colorByVar.hidden = true;
      }

      if (params.scores.colorBy === 'groups' && params.scores.plotType === 'p') {
         controls.scores.confEllipse.hidden = false;
      } else {
         controls.scores.confEllipse.hidden = true;
         params.scores.confEllipse = 'none';
      }

   })

   // combine main elements to be shared with all plots together
   const props = $derived({res, classres, model, simca, data, ncomp: simca.ncomp});

   // combine all callbacks together
   const callbacks = {markOutlier, showContributions};
</script>

{#snippet buttons1(sel)}
   <Container label="Selected">
      <Button onclick={() => markOutlier(sel)} text="remove" />
   </Container>
{/snippet}

{#snippet buttons2(sel)}
   <Container label="Selected">
      <Button onclick={() => showContributions(sel)} text="plot" />
      <Button onclick={() => markOutlier(sel)} text="remove" />
   </Container>
{/snippet}

{#if simca && testres}
<PlotsRouter {plots} {controls} {props} {callbacks} bind:params={params} />
{/if}