<script>
   /*************************************************************/
   /* AppPlots component for tes:new app                        */
   /* -----------------------------------                       */
   /* defines plot components, controls and parameters          */
   /*************************************************************/

   import { getDefaults } from 'svelte-controls-basic';
   import { Container, Button } from 'svelte-controls-basic';

   // global plot router
   import PlotsRouter from '../../../shared/plots/PlotsRouter.svelte';

   // import plot components
   import PlotAcceptance from '../../../shared/plots/PlotAcceptance.svelte';
   import PlotDistances from '../../../shared/plots/PlotDistances.svelte';
   import PlotScores from '../../../shared/plots/PlotScores.svelte';

   // import plot controls components
   import { getPlotAcceptanceControls, getPlotDistancesControls, getPlotScoresControls } from '../../../shared/plots/Controls.svelte.js';
    import { LINE_COLORS } from '../../../shared/plots/Defaults';

   let {
      model,
      simca,
      newset,
      newres,
      markOutlier,
      showContributions
   } = $props();

   // because most of the plot components expect JSON with results
   // assuming there is more than one result object we create it
   // same for the data
   const res = {'new': newres.pca};
   const data = {'new': newset};

   // this is derived as simca results can change without reloading the component
   const classres = $derived({'new': newres.simca});


   // JSON with plot components (scope is needed for providing correct properties)
   const plots = $derived({
      acceptance: {label: 'Acceptance', scope: 'objects', component: PlotAcceptance, buttons: buttons2},
      distances: {label: 'Distances', scope: 'objects', component: PlotDistances, buttons: buttons2},
      scores: {label: 'Scores', scope: 'objects', component: PlotScores, buttons: buttons1}
   })

   // JSON with plots controls (as Widget properties)
   const controls = $state({
      acceptance: getPlotAcceptanceControls(res),
      distances: getPlotDistancesControls(res),
      scores: getPlotScoresControls(res),
   })

   // JSON with currently selected plots parameters
   let params = $state({
      acceptance: getDefaults(controls.acceptance),
      distances: getDefaults(controls.distances),
      scores: getDefaults(controls.scores),
   });

   // add additional fixed parameters for the acceptance plot
   $effect(() => {
      params.acceptance['ncomp'] = simca.ncomp;
      params.acceptance['title'] = `Acceptance (A = ${simca.ncomp})`;
      params.acceptance['limType'] = simca.limType;
      params.acceptance['alpha'] = simca.alpha;
      params.acceptance['gamma'] = simca.gamma;
   });

   $effect(() => {
      params.acceptance['colorByObj'] = {
         values: classres.new.v[simca.ncomp - 1].decs,
         classColmap: {in: LINE_COLORS.new, out: LINE_COLORS.new},
         labels: ["in", "out"],
         filled: classres.new.v[simca.ncomp - 1].decs.map(v => v === 'in'),
         showBar: false
      }
   })

   // reactive effects for scores plot controls
   $effect(() => {
      if (params.scores.plotType !== 'p') {
         controls.scores.comp1.hidden = true;
         controls.scores.showEllipse.hidden = true;
         params.scores.showEllipse = false;
      } else {
         controls.scores.comp1.hidden = false;
         controls.scores.showEllipse.hidden = false;
      }

      if (params.scores.plotType === 'l' || params.scores.plotType === 'b') {
         controls.scores.colorBy.hidden = true;
         params.scores.colorBy = 'none';
      } else {
         controls.scores.colorBy.hidden = false;
      }

      if (params.scores.colorBy === 'variable') {
         controls.scores.colorByVar.hidden = false;
      } else {
         controls.scores.colorByVar.hidden = true;
      }

      if (params.scores.colorBy === 'groups' && params.scores.plotType === 'p') {
         controls.scores.confEllipse.hidden = false;
      } else {
         controls.scores.confEllipse.hidden = true;
         params.scores.confEllipse = 'none';
      }

   })

   // combine main elements to be shared with all plots together
   const props = $derived({res, classres, model, simca, data, ncomp: simca.ncomp});

   // combine all callbacks together
   const callbacks = {markOutlier, showContributions};
</script>

{#snippet buttons1(sel)}
   <Container label="Selected">
      <Button onclick={() => markOutlier(sel)} text="remove" />
   </Container>
{/snippet}

{#snippet buttons2(sel)}
   <Container label="Selected">
      <Button onclick={() => showContributions(sel)} text="plot" />
      <Button onclick={() => markOutlier(sel)} text="remove" />
   </Container>
{/snippet}

<PlotsRouter {plots} {controls} {props} {callbacks} bind:params={params} />